import React from "react";
import { NavLink } from "react-router-dom";

import { useStore } from "../hooks-store/store";
import classes from "./Navigation.module.css";

const Navigation = () => {
	const { user } = useStore(true)[0];
	return (
		<header className={classes.mainHeader}>
			<nav>
				<ul>
					<li>
						<NavLink to="/" activeClassName={classes.active} exact>
							<div className={classes.link}>Debtors</div>
						</NavLink>
					</li>
					<li>
						<NavLink to="/create" activeClassName={classes.active} exact>
							<div className={classes.link}>Add Debtors</div>
						</NavLink>
					</li>
					{user.isAuthenticated ? (
						<li>
							<NavLink to="/logout" activeClassName={classes.active} exact>
								<div className={classes.link}>Logout</div>
							</NavLink>
						</li>
					) : null}
				</ul>
			</nav>
		</header>
	);
};

export default Navigation;
