import { initStore } from "./store";

const updateUser = (curState, payload) => {
	let updatedUser;
	updatedUser = { ...curState.user, ...payload };
	return { user: updatedUser };
};

const logout = (curState, payload) => {
	let updatedUser;
	updatedUser = { ...curState.user, token: null, isAuthenticated: false };
	return { user: updatedUser };
};

const configureDebtorStore = () => {
	const actions = {
		UPDATE: (curState, payload) => updateUser(curState, payload),
		LOGOUT: (curState, payload) => logout(curState, payload)
	};

	initStore(actions, {
		user: {
			debtors: [],
			username: "",
			token: null,
			error: null,
			isAuthenticated: false
		}
	});
};

export default configureDebtorStore;
