import React from "react";

import Card from "../../UI/Card/Card";
import classes from "./Debtor.module.css";

const Debtor = props => {
	return (
		<Card>
			<h2 className={classes.header}>{props.debtor.name}</h2>
			{props.debtor.isDebtFree ? (
				<h3 className={classes.body}>Isn't Owing</h3>
			) : (
				<h3 className={classes.body}>
					Amount owed: {props.debtor.amount} Naira
				</h3>
			)}
		</Card>
	);
};

export default Debtor;
