import React, { useState, useEffect } from "react";
import axios from "axios";

import { useHistory } from "react-router-dom";
import { checkValidity } from "../../Utility/checkValidity";
import { useStore } from "../../components/hooks-store/store";
import Input from "../../components/UI/Input/Input";
import Button from "../../components/UI/Button/Button";
import classes from "./Auth.module.css";

const Auth = () => {
  const history = useHistory();
  const [{ user }, dispatch] = useStore();
  const [authForm, setAuthForm] = useState([
    {
      label: "Username",
      elementType: "input",
      elementConfig: {
        type: "text",
        placeholder: "Username",
      },
      value: "",
      validation: {
        required: true,
      },
      valid: false,
      touched: false,
    },
    {
      label: "Password",
      elementType: "input",
      elementConfig: {
        type: "password",
        placeholder: "Password",
      },
      value: "",
      validation: {
        required: true,
        minLength: 7,
      },
      valid: false,
      touched: false,
    },
  ]);
  const [isSignUp, setIsSignUp] = useState(true);
  const [formIsValid, setFormIsValid] = useState(false);

  const inputChangedHandler = (event, identifier) => {
    const updatedAuthForm = [...authForm];
    const identifierIndex = updatedAuthForm.findIndex(
      (d) => d.label === identifier
    );
    const updatedFormElement = {
      ...updatedAuthForm[identifierIndex],
    };
    updatedFormElement.value = event.target.value;
    updatedFormElement.valid = checkValidity(
      updatedFormElement.value,
      updatedFormElement.validation
    );
    updatedFormElement.touched = true;
    updatedAuthForm[identifierIndex] = updatedFormElement;

    let bufferFormIsValid = true;
    for (let inputIdentifier in updatedAuthForm) {
      bufferFormIsValid =
        updatedAuthForm[inputIdentifier].valid && bufferFormIsValid;
    }
    setFormIsValid(bufferFormIsValid);
    setAuthForm(updatedAuthForm);
  };

  useEffect(() => {
    if (user.isAuthenticated) {
      history.push("/");
    }
  });

  const switchAuthModeHandler = () => {
    setIsSignUp((prevState) => !prevState);
  };

  const submitHandler = async (event) => {
    event.preventDefault();
    let userData = {};
    for (let element of authForm) {
      let label;
      element.label === "Username"
        ? (label = "username")
        : (label = "password");
      userData[label] = element.value;
    }
    const apiCall = async (url, userData) => {
      let toUpdateFeedback;
      try {
        let { data } = await axios.post(url, userData);
        toUpdateFeedback = {
          token: data.token,
          username: data.username,
          isAuthenticated: true,
          error: null,
        };
        return toUpdateFeedback;
      } catch (error) {
        toUpdateFeedback = {
          error: error.response.data,
        };
        return toUpdateFeedback;
      }
    };
    let serverUrl = isSignUp ? "/api/signup" : "/api/login";
    let response = await apiCall(serverUrl, userData);
    dispatch("UPDATE", response);
  };

  let error;
  if (user.error) {
    error = <h3 style={{ color: "#780000" }}>{user.error}</h3>;
  }

  const form = (
    <form onSubmit={submitHandler}>
      {authForm.map((formElement) => (
        <Input
          key={formElement.label}
          label={formElement.label}
          elementType={formElement.elementType}
          elementConfig={formElement.elementConfig}
          invalid={!formElement.valid}
          touched={formElement.touched}
          value={formElement.value}
          changed={(event) => inputChangedHandler(event, formElement.label)}
        />
      ))}
      <Button btnType="Success" disabled={!formIsValid}>
        SUBMIT
      </Button>
    </form>
  );
  return (
    <div className={classes.Auth}>
      {error}
      {form}
      <Button onClick={switchAuthModeHandler} btnType="Danger">
        SWITCH TO {isSignUp ? "SIGNIN" : "SIGNUP"}
      </Button>
    </div>
  );
};

export default Auth;
