import React from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";

import Button from "../../components/UI/Button/Button";
import { useStore } from "../../components/hooks-store/store";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { NavLink, useParams } from "react-router-dom";
import Breakdown from "../../components/Debtors/DebtorDetailsBreakdown/DebtorDetailsBreakdown";

const DebtorDetails = () => {
  const [{ user }, dispatch] = useStore();
  const { debtorId } = useParams();
  const history = useHistory();

  let debtor;

  for (let element of user.debtors) {
    if (element._id === debtorId) {
      debtor = element;
      break;
    }
  }

  if (!debtor) {
    return <h2 style={{ textAlign: "center" }}>Error 404: User not Found</h2>;
  }

  const deleteUser = async () => {
    const apiCall = async (url) => {
      let toUpdateFeedback;
      try {
        await axios.delete(url, {
          headers: {
            authorization: "Bearer " + user.token,
          },
        });
        toUpdateFeedback = {
          error: null,
        };
        return toUpdateFeedback;
      } catch (error) {
        toUpdateFeedback = {
          error: error.response.data,
        };

        return toUpdateFeedback;
      }
    };
    let response = await apiCall(`/api/debtors/${debtorId}`);
    dispatch("UPDATE", response);
    history.push("/");
  };

  return (
    <>
      <div>
        <h2 style={{ textAlign: "center" }}>
          {debtor.isDebtFree
            ? `${debtor.name} isn't Owing You any Money`
            : `Total Amount Owed by ${debtor.name} is ${debtor.amount} Naira`}
          <NavLink to={`/debtors/${debtorId}/new`}>
            <FontAwesomeIcon
              icon={faPlus}
              transform="grow-10 up-10"
              style={{ float: "right", margin: "20px 20px", color: "black" }}
            />
          </NavLink>
        </h2>
      </div>
      {debtor.breakdown.map((b) => {
        return <Breakdown key={b.id} breakdown={b} />;
      })}
      {debtor.isDebtFree ? (
        <div style={{ textAlign: "center" }}>
          <Button onClick={deleteUser} btnType="Success">
            Delete User
          </Button>
        </div>
      ) : null}
    </>
  );
};

export default DebtorDetails;
