import { useEffect } from "react";
import { useHistory } from "react-router-dom";

import { useStore } from "../hooks-store/store";

const Logout = () => {
	const history = useHistory();
	const dispatch = useStore()[1];
	useEffect(() => {
		dispatch("LOGOUT");
		history.push("/");
	});

	return null;
};

export default Logout;
