import React, { useEffect } from "react";
import { NavLink, useHistory } from "react-router-dom";
import axios from "axios";

import Button from "../../components/UI/Button/Button";
import { useStore } from "../../components/hooks-store/store";
import Debtor from "../../components/Debtors/Debtor/Debtor";
import classes from "../Debtors/Debtors.module.css";

const Debtors = () => {
  const [{ user }, dispatch] = useStore();
  const history = useHistory();

  let toShow;
  useEffect(() => {
    const fetchData = async () => {
      const apiCall = async (url) => {
        let toUpdateUser;
        try {
          let { data } = await axios.get(url, {
            headers: {
              authorization: "Bearer " + user.token,
            },
          });
          toUpdateUser = { debtors: data.debtors };
          return toUpdateUser;
        } catch (error) {
          toUpdateUser = {
            error: error.response.data,
          };
          return toUpdateUser;
        }
      };
      let response = await apiCall("/api/debtors");
      dispatch("UPDATE", response);
    };
    if (user.isAuthenticated) {
      fetchData();
    }
  }, []);

  const changeToAuth = () => {
    history.push("/auth");
  };
  user.debtors.length === 0
    ? (toShow = (
        <h2 style={{ textAlign: "center" }}>
          {user.username}, no one is currently owing you money..... Lol
        </h2>
      ))
    : (toShow = (
        <div className={classes.debtorsList}>
          {user.debtors.map((debtor) => (
            <div key={debtor._id} className={classes.item}>
              <NavLink to={`/debtors/${debtor._id}`}>
                <Debtor debtor={debtor} />
              </NavLink>
            </div>
          ))}
        </div>
      ));

  if (!user.isAuthenticated) {
    toShow = (
      <div className={classes.debtorsList}>
        <h2>You are not Logged in.</h2>
        <Button
          onClick={changeToAuth}
          style={{ fontSize: "20px" }}
          btnType="Success"
        >
          Sign In
        </Button>
      </div>
    );
  }

  return toShow;
};
export default Debtors;
